export default class NextMonthTitle extends HTMLElement {
  constructor() {
    super();

    this.innerText = this.nextMonthTitle;
  }

  get nextMonthTitle() {
    const now = new Date();
    const currentMonth = now.getMonth();

    now.setMonth(currentMonth + 1);

    return now.toLocaleDateString(undefined, { month: "long" });
  }
}
